<template>
  <div class="ReferenceData">
      <div class="dataTop">
        <i>当前位置：首页</i>
        >
        <span>参考资料</span>
      </div>
      <div class="dataList" v-for="(item,index) in lists" :key="index">
        <div class="dataListTop clearfix">
          <div class="dataListTopL">
            <span>{{item.name}}</span>
            <b>|</b>
            <i>{{item.Market}}</i>
          </div>

          <div class="dataListTopR">
            <span>更多</span> <i class="el-icon-arrow-right"></i>
          </div>
        </div>
        <div class="dataListTopB">
          <ul>
            <li v-for="(data,index) in item.database " :key="index">
                  <span><router-link :to="'/WatchPdf/' + data.id">{{ data.description }}</router-link></span>
            </li>
          </ul>
        </div>
      </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      lists: [],
    };
  },
  methods: {
    // 获取资料细信息
    getData() {
      let _this = this;
      _this.$axios
        .post("/api/database-list", {

        })
        .then(function(res) {
          _this.lists = res.data.data.data;
        })
        .catch(function(error) {
          console.log(error);
      });
    }
  },
  created() {
    this.getData();
    this.$store.state.showHeader = "7";
  }
};
</script>
<style lang="less" rel="stylesheet/less">
@import "../../../static/css/base.css";
.ReferenceData {
  margin: 0 auto;
  width: 1280px;
  .dataTop {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background-color: #f1f1f1;
    margin-bottom: 15px;
    i {
      font-family: PingFangSC;
      font-size: 16px;
      text-align: left;
      color: #4a4a4a;
      margin-left: 59px;
    }
    span {
      margin: 0;
      font-family: PingFangSC;
      font-size: 16px;
      color: #4a90e2;
    }
  }
  .dataList {
    float: left;
    margin: 0 55px 25px 55px;
    .dataListTop {
      width: 516px;
      border-bottom: 1px solid #c7c7c7;
      margin: 0 0 19.5px 0;
      .dataListTopL {
        float: left;
        span {
          font-family: PingFangSC;
          font-size: 20px;
          font-weight: 500;
          text-align: left;
          color: #4a90e2;
          border-bottom: 3px solid #4a90e2;
        }
        b {
          display: inline-block;
          width: 3px;
          height: 21px;
        }
        i {
          font-family: PingFangSC;
          font-size: 16px;
          font-weight: 500;
          text-align: left;
          color: #abaaab;
        }
      }
      .dataListTopR {
        float: right;
        span {
          line-height: 31px;
          font-family: PingFangSC;
          font-size: 16px;
          text-align: right;
          color: #abaaab;
        }
      }
    }
    .dataListTopB {
          height: 300px;
          overflow: hidden;
      ul {
        margin-left: 20px;
        li {
          list-style-type: square;
          color:red;
          font-size: 16px;
          i {
            font-family: PingFangSC;
            font-size: 16px;
            line-height: 1.88;
            text-align: left;
            color: #4a4a4a;
          }
        }
        li:first-child {
          color: red;
          font-size: 16px;
          span {
            font-family: PingFangSC;
            font-size: 16px;
            font-weight: 500;
            text-align: left;
            color: #4a4a4a;
          }
        }
      }
    }
  }
}
</style>



